body, html {
    margin: 0;
    padding: 0;
    height: 100%;
    width: 100%;
    overflow-x: hidden; /* Prevent horizontal scroll */
}

.top-container {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    background-color: white; /* Background color of the top nav is white */
    padding: 1rem 10%;
    box-sizing: border-box;
}

.top-nav {
    display: flex;
    gap: 20px;
}

.nav-link {
    text-decoration: none;
    color: black; /* Navigation links are black */
    font-weight: bold; /* Makes the nav links bold */
    transition: color 0.3s;
}

.nav-link:hover {
    color: #5e72eb; /* Hover color is now blue */
}

.header-main {
    display: flex;
    justify-content: center;
    align-items: center;
    height: calc(100vh - 50px); /* Adjust the height based on the top nav's height */
    background-color: white; /* Use the exact color from your background */
    color: black; /* This should be the color that matches your design */
    text-align: center;
    flex-direction: column;
    box-sizing: border-box;
}

.intro-text {
    max-width: 600px; /* Adjust based on your content width */
}

.intro-text h1 {
    font-size: 4em;
    margin-bottom: 0.5em;
    color:#5e72eb
}

.intro-text p {
    font-size: 1.5em;
    color:#5e72eb;
}

@media (max-width: 768px) {
    .top-container {
        justify-content: center;
    }

    .top-nav {
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: center;
        gap: 10px;
    }

    .nav-link {
        margin-bottom: 0; /* Removes additional space on mobile */
    }

    .header-main {
        padding: 15px; /* Adds padding on mobile for better spacing */
    }

    .intro-text h1 {
        font-size: 2em; /* Adjust font size for mobile */
    }

    .intro-text p {
        font-size: 1em; /* Adjust font size for mobile */
    }
}
